import React from 'react';
import PropTypes from 'prop-types';
import Link from 'gatsby-link';

const ComponentPage = ({ data: { componentsMetadata } }) => {
  const componentData = componentsMetadata.edges[0].node;

  return (
    <div>
      <h1>{componentData.displayName}</h1>
      <p> {componentData.description && componentData.description.text}</p>

      <h4>Props:</h4>
      <table>
        <tr>
          <th>Name</th>
          <th>Required</th>
          <th>Options</th>
        </tr>
        {componentData.props.map((prop, i) => (
          <tr {...{ key: i }}>
            <td>{prop.name}</td>
            <td>{prop.required ? 'yes' : 'no'}</td>
            <td>
              {prop.type.value &&
                prop.type.value.map &&
                prop.type.value.map(v => v.value).join(', ')}
            </td>
          </tr>
        ))}
      </table>
      <Link to="/">Go back to the homepage</Link>
    </div>
  );
};

ComponentPage.propTypes = {
  data: PropTypes.shape({ componentsMetadata: PropTypes.object }).isRequired
};

export default ComponentPage;

export const pageQuery = graphql`
  query ComponentQuery($name: String!) {
    componentsMetadata: allComponentMetadata(
      filter: { displayName: { eq: $name } }
    ) {
      edges {
        node {
          displayName
          description {
            text
          }
          props {
            name
            type {
              value
              raw
            }
            required
          }
        }
      }
    }
  }
`;



